<template>
  <div>
    <img src="../assets/images/img_1.png" />
    <img src="../assets/images/img_2.jpg">
    <div class="hello">
      简陋的实现一下<br />
      啊啊啊里里里里登录<br />
      账号密码 admin 123456<br />
      <input type="text" v-model="userName" placeholder="账号号号"><br />
      <input type="password" v-model="passWord" placeholder="密码码码"><br />
      <div @click="signIn()" type="submit">登陆</div>
    </div>
  </div>
</template>

<script>
import qs from 'qs'
export default {
  name: 'HelloWorld',
  data () {
    return {
      userName: '',
      passWord: ''
    }
  },
  methods: {
    signIn () {
      const that = this
      this.axios.post('http://111.229.222.96:8081/ali/signIn',
        qs.parse({
          userName: this.userName,
          passWord: this.passWord
        })
      ).then(function (response) {
        console.log(response.data)
        if (response.data === 'success') {
          console.log('登录成功')
          that.$router.push({path: '/HelloWorld2'})
        }
        if (response.data === 'falseeee') {
          alert('账户&密码错误')
        }
      }).catch(function (error) {
        console.log(13333 + error)
      })
    }
  }
}
</script>
<style>
  .hello{
   margin: 0,auto;
   top: 12.5rem;
   left: 37.5rem;
   position: absolute;
   background-color: red;
  }
</style>



